export default "import { NgModule } from \"@angular/core\";\nimport { FormsModule, ReactiveFormsModule } from \"@angular/forms\";\nimport { RouterModule } from \"@angular/router\";\nimport {\n    DEMO_PATH_TOKEN,\n    NuiButtonModule,\n    NuiCommonModule,\n    NuiDocsModule,\n    NuiSearchModule,\n    NuiSelectModule,\n    NuiTextboxModule,\n    SrlcStage,\n} from \"@nova-ui/bits\";\n\nimport {\n    SearchBasicExampleComponent,\n    SearchDocsExampleComponent,\n    SearchFocusChangeExampleComponent,\n    SearchInputChangeExampleComponent,\n    SearchOnSearchCancelExampleComponent,\n    SearchPlaceholderExampleComponent,\n    SearchTestExampleComponent,\n    SearchVisualTestComponent,\n} from \"./index\";\n\nconst routes = [\n    {\n        path: \"\",\n        component: SearchDocsExampleComponent,\n        data: {\n            \"srlc\": {\n                \"stage\": SrlcStage.ga,\n            },\n            showThemeSwitcher: true,\n        },\n    },\n    {\n        path: \"search-test\",\n        component: SearchTestExampleComponent,\n        data: {\n            \"srlc\": {\n                \"hideIndicator\": true,\n            },\n        },\n    },\n    {\n        path: \"search-visual-test\",\n        component: SearchVisualTestComponent,\n        data: {\n            \"srlc\": {\n                \"hideIndicator\": true,\n            },\n        },\n    },\n];\n\n@NgModule({\n    imports: [\n        NuiButtonModule,\n        NuiSearchModule,\n        NuiTextboxModule,\n        NuiDocsModule,\n        NuiCommonModule,\n        FormsModule,\n        ReactiveFormsModule,\n        RouterModule.forChild(routes),\n        NuiSelectModule,\n    ],\n    declarations: [\n        SearchTestExampleComponent,\n        SearchBasicExampleComponent,\n        SearchDocsExampleComponent,\n        SearchFocusChangeExampleComponent,\n        SearchInputChangeExampleComponent,\n        SearchOnSearchCancelExampleComponent,\n        SearchPlaceholderExampleComponent,\n        SearchVisualTestComponent,\n    ],\n    providers: [\n        {\n            provide: DEMO_PATH_TOKEN,\n            useFactory: () => (<any>require).context(`!!raw-loader!./`, true, /.*\\.(ts|html|less)$/),\n        },\n    ],\n    exports: [\n        RouterModule,\n    ],\n})\nexport class SearchModule {\n}\n";